@font-face {
    font-family: Panton;
    font-weight: bold;
    src: url("/fonts/panton/Panton-BlackCaps.otf") format("opentype");
}

@font-face {
    font-family: Panton;
    font-weight: normal;
    src: url("/fonts/panton/Panton-LightCaps.otf") format("opentype");
}

body {
    background-color: #e6e6e6;
    font-family: 'Lato', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: #020715;
}

header {
    background-image: url('/img/elements/header-background.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

a, a:hover, a:focus {
    text-decoration: none;
}

.form-control {
    padding: 10px 18px;
    height: 50px;
}

.form-inline .form-group {
    padding: 0 5px;
}

.btn-yellow {
    padding: 13px 25px;
    background-color: #fdca10;
    color: #132351;
    text-transform: uppercase;
    font-size: 14px;
}

.top-nav {
    padding: 15px 0;
}

.top-nav__text {
    padding-top: 25px;
    font-family: 'Panton', sans-serif;
    font-size: 54px;
    font-weight: bold;
    color: #FFF;
    line-height: 1;
}

.auth-nav {
    text-align: right;
}

.auth-nav__list {
    display: inline-block;
    list-style-type: none;
}

.auth-nav__item {
    float: left;
    padding: 0 5px;
}

.auth-nav__link {
    display: inline-block;
    padding: 11px 15px;
    position: relative;
    color: #FFF;
}

.auth-nav__link--business {
    background-color: #ca0b23;
}

.auth-nav__link--business:before, .auth-nav__link--business:after {
    position: absolute;
    top: 0;
    content: ' ';
    width: 20px;
    height: 47px;
}

.auth-nav__link--business:before {
    left: -20px;
    background-image: url('/img/elements/triangle-left.png');
}

.auth-nav__link--business:after {
    right: -20px;
    background-image: url('/img/elements/triangle-right.png');
}

.FrontPageSearchForm {
    position: relative;
    height: 100%;
}

.FrontPageSearchForm__positioner {
    position: absolute;
    top: 30%;
    left: 0;
    width: 100%;
    text-align: center;
}

.FrontPageSearchForm__positioner form {
    display: inline-block;
}

.FrontPageSearchForm form {
    padding: 10px 5px;
    background-color: rgba(19, 35, 81, 0.5);
    border-radius: 4px;
}

.Logo {
    padding: 25px 0;
}

.Logo img {
    margin: 0 auto;
}

.EncourageText {
    margin-bottom: 25px;
}

.EncourageText__header, .EncourageText__bottom {
    color: #FFF;
    margin: 0;
    line-height: 1.3;
}

.EncourageText__header {
    font-weight: 900;
    font-size: 36px;
}

.EncourageText__bottom {
    font-size: 24px;
}

.CategoriesWrapper {
    text-align: left;
    height: 80%;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #ca0b23;
}

.CategoriesWrapper:before {
    position: absolute;
    top: -52px;
    left: 0;
    content: ' ';
    width: 100%;
    height: 52px;
    background-image: url('/img/elements/triangle-categories.png');
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.CategoriesMenu {
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding: 30px 0;
    margin: 0 0 0 100px;
    height: 100%;
}

.CategoriesMenu__item {
    flex: 1;
}

.CategoriesMenu__item a {
    position: relative;
    display: block;
    color: #FFF;
}

.CategoriesMenu__item a:before {
    content: ' ';
    position: absolute;
    top: -10px;
    left: -50px;
    width: 40px;
    height: 40px;
    background-repeat: no-repeat;
    background-position: center;
}

.CategoriesMenu__item--dance a:before {
    background-image: url('/img/icons/dance.png');
}

.CategoriesMenu__item--recreation a:before {
    background-image: url('/img/icons/weight.png');
}

.CategoriesMenu__item--fitness a:before {
    background-image: url('/img/icons/ball.png');
}

.CategoriesMenu__item--sport a:before {
    background-image: url('/img/icons/dance.png');
}

.CategoriesMenu__item--beauty a:before {
    background-image: url('/img/icons/weight.png');
}

.CategoriesMenu__item--music a:before {
    background-image: url('/img/icons/ball.png');
}

.CategoriesMenu__item--education a:before {
    background-image: url('/img/icons/dance.png');
}

.PromotedOffers {
    margin-top: -100px;
    padding-bottom: 25px;
}

.PromotedOffers > .container > .row:first-child {
    margin-bottom: 15px;
}

.PromotedOffers__sectionTitle {
    text-transform: uppercase;
    color: #FFF;
    font-weight: bold;
    margin: 0;
}

.PromotedOffers__scrollDown {
    text-align: right;
    color: #FFF;
    font-size: 13px;
    padding-right: 25px;
    position: relative;
}

.PromotedOffers__scrollDown:after {
    font-family: FontAwesome;
    content: "\f107";
    display: block;
    vertical-align: middle;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
    font-size: 18px;
}

.PromotedOffers__offer {
    background-color: #FFF;
    padding: 10px;
}

.PromotedOffers__offer__imageWrapper {
    position: relative;
}

.PromotedOffers__offer__imageWrapper img {
    max-width: 100%;
    position: relative;
    z-index: 0;
}

.PromotedOffers__offer__imageWrapper__cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(2, 7, 21, 0.1);
    z-index: 5;
}

.PromotedOffers__offer__imageWrapper__tag {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #ca0b23;
    padding: 3px 2px 3px 7px;
    color: #FFF;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 12px;
}

.PromotedOffers__offer__imageWrapper__tag:after {
    position: absolute;
    top: 0;
    right: -20px;
    width: 20px;
    height: 25px;
    content: ' ';
    background-image: url('/img/elements/triangle-tag.png');
    background-repeat: no-repeat;
    background-size: cover;
}

.PromotedOffers__offer__companyLogoWrapper {
    margin: -55px auto 0 auto;
    width: 110px;
    height: 110px;
    border: 5px solid #FFF;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background-color: #FFF;
    position: relative;
    z-index: 15;
    margin-bottom: 15px;
}

.PromotedOffers__offer__companyLogo__background {
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    position: relative;
}

.PromotedOffers__offer__companyLogo__background--black {
    background-color: #000;
}

.PromotedOffers__offer__companyLogo__background--white {
    background-color: #FFF;
    border: 1px solid #cacaca;
}

.PromotedOffers__offer__companyLogo__background--red {
    background-color: #ce0809;
}

.PromotedOffers__offer__companyLogo__background img {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
    max-width: 80%;
}

.PromotedOffers__offer__title {
    text-align: center;
    font-weight: 900;
    font-size: 20px;
}

.PromotedOffers__offer__address {
    text-align: center;
    color: rgba(2, 7, 21, .6);
    margin-bottom: 15px;
}

.PromotedOffers__offer__calendar {
    color: #0e2f86;
    font-weight: bold;
    font-size: 14px;
}

.PromotedOffers__offer__calendar:before {
    display: inline-block;
    width: 19px;
    height: 20px;
    content: ' ';
    background-image: url('/img/icons/calendar.png');
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    vertical-align: -3px;
    margin-right: 7px;
}

.PromotedOffers__offer__reviews {
    text-align: right;
}

.stars {
    display: inline-block;
}

.stars__star {
    display: inline-block;
    width: 15px;
    height: 15px;
}

.stars__star:before {
    font-family: FontAwesome;
    content: ' ';
    display: block;
    vertical-align: middle;
    font-size: 18px;
}

.stars__star--full:before {
    content: "\f005";
}

.stars__star--half:before {
    content: "\f123";
}

.stars__star--empty:before {
    content: "\f006";
}

.PromotedOffers__offer__reviews p {
    color: rgba(2, 7, 21, .6);
    font-size: 12px;
}

@media (min-width: 1200px) {
    aside {
        width: 14%;
        position: fixed;
        top: 0;
        left: 0;
        height: 100%;
        background-color: #0e2f86;
    }

    .container {
        width: 94%;
    }

    .Middle {
        margin-left: 14%;
    }
}